.App {
  display: flex;
  text-align: center;
  flex-direction: column;
  background-color: palegoldenrod;
}
#row1{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: palegoldenrod;
}

.Component{
  display: flex;
  text-align: center;
  flex-direction: row;
  margin: auto;
  background-color: lightgray;
  /* border with rounded corners in gray */
  border: 1px solid black;
  border-radius: 5px;
  padding: 2px;

}

.buttonWrapper{
  display: flex;
  text-align: center;
  flex-direction: column;
  margin: auto;
}

.customBtn {
  display: inline-block;
  outline: 0;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 3px;
  color: #fff;
  background: #000;
  font-size: 20px;
  font-weight: 600;
  line-height: 0.5rem;
  padding: 12px 20px;
  text-align:center;
  transition-duration: .15s;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
}
 .customBtn:hover{
  color: #000;
  background: rgb(255, 218, 87);
}
.clearList{
  list-style-type: none;
}

#primaryVPHolder{
  display: flex;
  /* set width to full screen */
  width: 100%;
    flex-direction: row;
  background-color: yellow;
  
}
.VPDiv{
display: flex;
width: 20%;
flex-direction: column;
justify-content: center;
border-style: solid;
border-width: 1px;
border-color: black;
}
